import {locationData,cityDetail} from '../../commons/js/list.js';
export default {
	name:'city',
	data() {
		return {
			cityVal:true,
			keywords:'',
			addressVal:'无锡市',
			city:[
				{
					name:'无锡市'
				},
				{
					name:'南京市'
				},
				{
					name:'苏州市'
				},
				{
					name:'常州市'
				},
				{
					name:'徐州市'
				}
			],
			citydata:[]
		}
	},
	onLoad(e) {
		this.addressVal = e.name
	},
	methods: {
		// 点击定位取到城市名
		clickCity(){
			this.$store.commit('citymuta',this.addressVal)
			uni.switchTab({
				url:'../strategy/strategy'
			})
		},
		// 取到热门城市
		hotcity(city){
			this.$store.commit('citymuta',city)
			// 注：跳转回tab页面不能再使用navigateTo方法，该方法有层级限制，详见开发者文档
			// 该方法不能带参数跳转 navigateTo是可以带参数的
			uni.switchTab({
				url:'../strategy/strategy'
			})
		},
		// input事件
		searchInput(e){
			cityDetail(e.target.value)
			.then(res => {
				// 取出城市名
				let cityname = res[0].city;
				let cityArray = res.map((item) => {
					return item.title
				})
				this.citydata = [cityname,...cityArray]
			})
			.catch(err => {
				console.log(err)
			})
		},
		// 关闭搜索框
		cancle(){
			this.cityVal = true;
			this.keywords = '';
			this.citydata = [];
		},
		seekCity(city){
			this.keywords = city;
			this.$store.commit('citymuta',city);
			uni.switchTab({
				url:'../strategy/strategy'
			})
		},
	}
}
